.whiteBg {
    background: rgb(40, 44, 52);
    width: 100%;
}
.transparent {
    background: transparent;
}
.jobStyle {
    cursor: pointer;
    font-size: 14px;
    line-height: 1rem;
    font-style: italic;
    color: rgba(255, 255, 255, 0.7);
    display: flex;
    align-items: center;
}
.green {
    color: #deffbe!important;
    cursor: pointer;
    margin-left: 7px;
}
.red {
    color: rgb(244, 33, 46)!important;
    cursor: pointer;
    margin-left: 7px;
}
.green label {
    margin-right: 7px;
    color: #deffbe!important;
    cursor: pointer;
}
.red label {
    margin-right: 7px;
    cursor: pointer;
    color: rgb(244, 33, 46)!important;
}
.icon {
    margin-right: 7px;
    cursor: pointer;
}
.additionalInf {
    position: absolute;
    transition: opacity .4s ease-in-out;
    min-height: 70px;
    justify-content: space-around;
    display: flex;
    box-shadow: rgba(136, 153, 166, 0.2) 0px 0px 15px,
    rgba(136, 153, 166, 0.15) 0px 0px 3px 1px;
    align-items: center;
    border-radius: 30px;
    flex-direction: column;
    top: -100vh;
    bottom: auto;
    opacity: 0;
    z-index: 99;
    right: -10px;
    width: 200%;
    padding: 20px;
    background: #181818;
}
.additionalInfActive {
    transition: opacity .4s ease-in-out;
    z-index: 99;
    right: 20px;
    padding: 20px;
    bottom: auto;
    opacity: 1;
    top: 0;
    position: absolute;
    min-height: 70px;
    justify-content: space-around;
    display: flex;
    box-shadow: rgb(136 153 166 / 20%) 0px 0px 15px, rgb(136 153 166 / 15%) 0px 0px 3px 1px;
    align-items: center;
    border-radius: 30px;
    flex-direction: column;
    background: #181818;
    min-width: 350px;
}
.additionalInfActive::after {
    content: '';
    width: 20px;
    height: 20px;
    display: block;
    position: absolute;
    top: -10px;
    right: 28px;
    transform: rotate(45deg);
    background: #181818;
    z-index: 10008;
}